.insights-area-wrapper-no-data {
    width: 100%;
    height: 100%;
    padding: 15px;
    box-sizing: border-box; /* to account for the padding */
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 5px;
}

.insights-area-wrapper {
    width: 100%;
    height: 100%;
    padding: 15px;
    box-sizing: border-box; /* to account for the padding */
    display: flex;
    flex-direction: column;
    gap: 5px;
}

.overview-marketsegments {
    width: 100%;
    padding: 15px;
    box-sizing: border-box; /* to account for the padding */
    display: flex;
    gap: 25px;
    flex-direction: row;
}

.overview-insigths {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    gap: 10px;
}

.demographic-info {
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    flex-direction: row; 
    gap: 20px;
}

.demographic-info-element {
    display: flex;
    flex-direction: row;
    gap: 5px;
}

.demographic-info-text {
    max-width: 450px !important;  
    overflow: hidden !important;
    white-space: nowrap !important; 
    text-overflow: ellipsis !important; 
    display: block; 
}

.total-investments-cards {
    display: flex;
    flex-direction: row;
    gap: 10px;
    width: 100%;
}

.information-card {
    display: flex;
    flex-direction: column;
    background-color: white;
    border-radius: 5px;
    padding: 10px;
}

.investment-areas-wrapper {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: row;
    gap: 10px;
}

.investment-areas {
    display: flex;
    flex-direction: column;
    background-color: white;
    border-radius: 5px;
    height: 100%;
    width: 50%;
}

.no-investment-areas {
    display: flex;
    flex-direction: column;
    background-color: white;
    border-radius: 5px;
    height: 100%;
    width: 50%;
    justify-content: center;
}

.investment-areas-title {
    background-color: rgb(65, 152, 215, 0.1);
    padding: 5px;
    padding-left: 10px;
    border-top-left-radius: 5px;
    border-top-right-radius: 5px;
}

.investment-areas-list {
    display: flex;
    flex-direction: column;
    padding: 5px;
    padding-left: 10px;
    max-height: 105px;
    gap: 5px;
    overflow: auto;
    justify-content: flex-start;
    align-items: flex-start;
}

.investments-graphs {
    display: flex;
    flex-direction: row;
    gap: 100px;
    box-sizing: border-box;
    padding: 15px;
}

.chart-wrapper {
    display: flex;
    flex-direction: column;
    width: 50%;
    height: 100%;
    box-sizing: border-box;
    gap: 10px;
}

.tooltip {
    background-color: 'white';
    padding: 10px; 
    position: absolute;
    border-radius: 5px;
    color: #3C3E66 !important;
}